import React, { Component, useState } from 'react'
import '../App.css';
import { Button, Form, FormGroup, Label, Input, FormText, Col, Row } from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { display } from '@material-ui/system';
import { Card, CardHeader, Table, TableRow, TableCell } from '@material-ui/core';
import TableHead from '@material-ui/core/TableHead';
import { Redirect, Link, Router, Switch, Route } from 'react-router-dom';
import Carselection from './Carselection';
import Modal from 'react-modal';

export class Bookingpage extends Component {

    openmodal= () => {
        
        return alert("Booking Confirmed")
            
        
    }

    render() {
        return (
            <Form>
                <div className="App">Rentvroom</div>
                <div className="container-fluid center">
                    <Form>
                        <Row form style={{ marginBottom: "5%" }}>
                            <h1>Booking Details</h1>
                        </Row>
                        <Row form>
                            <Col md={6} >
                                <FormGroup>
                                    <Label for="name">Name</Label>
                                    <Input type="text" name="name" id="username" placeholder="Enter your name" />
                                </FormGroup>
                            </Col>
                            <Col md={6}>
                                <FormGroup>
                                    <Label for="number">Contact Number</Label>
                                    <Input type="text" pattern="[0-9]*" inputMode="numeric" name="number" id="usernumber" placeholder="+91 Enter your number" />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row form>
                            <Col md={6}>
                                <FormGroup>
                                    <Label for="issuedate">Issue Date</Label>
                                    <Input
                                        type="date"
                                        name="issuedate"
                                        id="issueDate"
                                        placeholder=""
                                    />
                                </FormGroup>
                            </Col>
                            <Col md={6}>
                                <FormGroup>
                                    <Label for="returndate">Return Date</Label>
                                    <Input
                                        type="date"
                                        name="returndate"
                                        id="returnDate"
                                        placeholder="date placeholder"
                                    />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row form>
                            <Col md={6}>
                                <FormGroup>
                                <Link to="/"><Button outline color="secondary">Back</Button></Link>
                                </FormGroup>
                            </Col>
                            <Col md={6}>
                                <FormGroup style={{ textAlign: "end" }}>
                                    <Button outline color="success" onClick={this.openmodal}  >Book Now</Button>
                                </FormGroup>
                            </Col>
                        </Row>
                    </Form>
                </div>

            </Form>

        )
    }
}

export default Bookingpage
